import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    showStudy:false,
    showToast:false,
    toastTitle:'提交成功'
  },
  mutations: {
    toggleShow(state,flag){
      state.showStudy = flag;
    },
    toggleToast(state,toast){
      state.showToast = toast;
    },
    setTitle(state,title){
      state.toastTitle = title;
    }
  },
  actions: {}
});
