<template>
    <div class="news-detail">
        <Banner :imgUrl="imgUrl"/>
        <div class="container">
            <div class="title">{{newsData.title}}</div>
            <div class="time">发布时间：{{newsData.publish_time}}</div>
            <div class="con">
                <div v-html="content"></div>
            </div>
            <!-- <div class="page">
                <p @click="goDetail(before.cid,before.id)" v-if="before">【上一篇】{{before.title}}</p>
                <p @click="goDetail(after.cid,after.id)" v-if="after">【下一篇】{{after.title}}</p>
            </div> -->
        </div>
    </div>
</template>
<script>
import Banner from '../../components/Banner.vue'
import {IMAGE_PATH} from '../../common/pathConfig.js';
export default {
    data(){
        return{
            newsData:{},
            before:{},
            after:{},
            imgUrl:'',
            content:'',
            imgPath:IMAGE_PATH
        }
    },
    created(){
        this.getData();
        this.getBanner();

    },
    components:{
        Banner
    },
    methods:{
        getBanner(){
            this.$http.get('/api/show/TopPic',{
                params:{
                    id:35
                }
            }).then(res=>{
                if (res.data.state === 200){
                    this.imgUrl = res.data.data.cat_pic || '';
                }
            })
        },
        // goDetail(cid,id){
        //     this.$router.push({path:'/news/newsDetail',query:{cid:cid,arcId:id}});
        //     this.getData(cid,id);
        // },
        getData(){
            this.$http.get('/api/show/index',{
                params:{
                   id:35
                }
            }).then(res=>{
                if(res.data.state === 200){
                    this.newsData = res.data.data[0];
                    const str = res.data.data[0].content;
					let newStr = null;
                    const imgSrc = str.match(/[sS][rR][cC][\s]*=[\s']*(?<ref_value>.*?(\.gif|\.jpg|\.png))/gi);
                    let imgRes = [];
                    imgSrc && imgSrc.forEach(item => {
                        imgRes.push(item.substr(6));
                    });
                    imgRes.forEach(item => {
                        const regExp = new RegExp(`/${item}`, 'gi');
                        newStr = str.replace(regExp, `${this.imgPath}/${item}`);
                    });
                    this.content = newStr || str;
                }
            })
        }
    }
}
</script>
