<template>
    <div>
        <Banner :imgUrl='imgUrl'/>
        <div class="about-bg">
            <div class="container" v-for="item in bgList" :key="item.arc_id">
                <div class="title">{{item.title}}</div>
                <div class="row" >
                    <div class="col-xs-12 col-sm-12 col-md-12">
                        <div class="con">
                            <div v-html="item.content"></div>
                        </div>
                        <div class="img">
                            <img :src="imgPath+item.litpic" alt="">
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="about-si">
            <div class="container">
                 <div class="title">SI展示</div>
                <div class="row" v-for="index in Math.ceil(showList.length/4)" :key="index">
                    <div class="col-xs-12 col-sm-6 col-md-3 item" v-for="item in showList.slice((index-1)*4,index*4)" :key="item.id">
                        <dl>
                            <dt>
                               <img :src="imgPath+item.litpic" alt=""> 
                            </dt>
                            <dd>
                                <p class="title">{{item.title}}</p>
                                <p class="con">{{item.description}}</p>
                            </dd>
                        </dl>
                    </div>
                </div>
            </div>
        </div>
        <div class="about-idea">
            <div class="container" v-for="item in ideaList" :key="item.arc_id">
                <div class="row">
                    <div class="col-xs-12 col-sm-12 col-md-6 left">
                        <img :src="imgPath+item.litpic" alt="">
                    </div>
                    <div class="col-xs-12 col-sm-12 col-md-6 right">
                        <div class="title">{{item.title}}</div>
                        <div class="con">
                            <div v-html="item.content"></div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import Banner from '../../components/Banner';
import {IMAGE_PATH} from '../../common/pathConfig.js';
export default {
    data () {
        return {
            imgUrl:'',
            bgList:[],
            imgPath:IMAGE_PATH,
            showList:[],
            ideaList:[]
        }
    },
    components:{
        Banner
    },
    created(){
        this.getBanner();
        this.getBg();
        this.getSiShow();
        this.getIdea();
    },
    methods:{
        getBanner(){
            this.$http.get('/api/show/TopPic',{
                params:{
                    id:9
                }
            }).then(res=>{
                if (res.data.state === 200){
                    this.imgUrl = res.data.data.cat_pic;
                }else{
                    this.imgUrl = '';
                }
            })
        },
        //乐思乐享背景
        getBg(){
            this.$http.get('/api/show/index',{
                params:{
                    id:14
                }
            }).then(res=>{
                if(res.data.state === 200){
                    this.bgList = res.data.data || [];
                }
            })
        },
        //思展示
        getSiShow(){
            this.$http.get('/api/show/index',{
                params:{
                    id:15
                }
            }).then(res=>{
                if(res.data.state === 200){
                    this.showList = res.data.data || [];
                }
            })
        },
        //品牌理念
        getIdea(){
            this.$http.get('/api/show/index',{
                params:{
                    id:16
                }
            }).then(res=>{
                if(res.data.state === 200){
                    this.ideaList = res.data.data || [];
                }
            })
        }
    }
}
</script>
