<template>
  <div class="banner-list">
    <swiper :options="swiperOption" ref="SwiperCourse" id="swiper_container">
        <swiper-slide v-for="item in list" :key="item.id" >
          <div class="img"><img :src="imgPath+item.litpic" alt /></div>
          <div class="text"><p>{{item.description}}</p></div>
        </swiper-slide>
        <div class="swiper-button-prev" slot="button-prev"></div>
        <div class="swiper-button-next" slot="button-next"></div>
    </swiper>
  </div>
</template>
<script>
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";
import {IMAGE_PATH} from '../common/pathConfig.js'
export default {
  name: "SwiperCourse",
  props:{
    list:{
      type:Array,
      default(){
        return []
      }
    }
  },
  data() {
    return {
      swiperOption: {
        autoplay: true,
        navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
        },
        observer: true, 
        observeParents: true
      },
      imgPath:IMAGE_PATH
    };
  },
  computed: {
    swiper() {
      return this.$refs.SwiperCourse.swiper;
    }
  },
  components: {
    swiper,
    swiperSlide
  },
};
</script>

