<template>
    <div>
        <Banner :imgUrl='imgUrl'/>
        <div class="concat">
            <div class="title">联系我们</div>
            <div class="container">
                <div class="map">
                    <div class="amap-page-container">
                    <div class="amap-page-container">
                        <el-amap vid="amap" :zoom="zoom" :center="center" class="amap-demo">
                        <el-amap-info-window
                            :position="currentWindow.position"
                            :content="currentWindow.content"
                            :visible="currentWindow.visible"
                            :events="currentWindow.events"
                        ></el-amap-info-window>
                        </el-amap>
                    </div>
                    </div>
                </div>
                <div class="info-list">
                    <div class="row">
                        <div class="col-xs-12 col-sm-4 col-md-4" v-for="item in iconList" :key="item.id">
                            <p class="icon" :class="'iconfont '+ item.icon"></p>
                            <p class="title">{{item.title}}</p>
                            <p class="text">{{item.text}}</p>
                        </div>
                    </div>
                </div>
            </div>
            <div class="message">
                <div class="tips">在一切开始前，先说说您的需求吧</div>
                <div class="msg-box">
                    <div class="form-box">
                        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                            <el-col :span="formSpan">
                                <el-form-item label="" prop="name">
                                    <el-input v-model="ruleForm.name" placeholder="请输入您的姓名"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="formSpan">
                                <el-form-item label="" prop="tel">
                                    <el-input v-model="ruleForm.tel" placeholder="请输入您的电话"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="formSpan">
                                <el-form-item label="" prop="email">
                                    <el-input v-model="ruleForm.email" placeholder="请输入您的邮箱"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="formSpan">
                                <el-form-item label="" prop="msg">
                                    <el-input type="textarea" :rows="textRow"  v-model="ruleForm.msg" placeholder="请留言您想说的话"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="formSpan">
                                <el-form-item>
                                    <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
                                </el-form-item>
                            </el-col>
                        </el-form>
                    </div>
                </div>
            </div>
        </div>
        
    </div>
</template>
<script>
import Banner from '../../components/Banner';
import VueAMap from "vue-amap";
import { AMapManager } from "vue-amap";
import {mapState,mapMutations} from 'vuex';
let amapManager = new VueAMap.AMapManager();
export default {
    data () {
        var checkPhone = (rule, value, callback) => {
            if (!value) {
                return callback(new Error('手机号不能为空'));
            } else {
                const reg = /^1[3|4|5|7|8][0-9]\d{8}$/
                if (reg.test(value)) {
                    callback();
                } else {
                    return callback(new Error('请输入正确的手机号格式'));
                }
            }
        };
        var checkEmail = (rule, value, callback) => {
            const mailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/
            if (!value) {
                return callback(new Error('邮箱不能为空'))
            }else{
                if (mailReg.test(value)) {
                    callback()
                } else {
                    callback(new Error('请输入正确的邮箱格式'))
                }
            }
        };
        return {
            zoom: 16,
            center: [118.164550, 39.659639],
            windows: [
                {
                position: [118.164550, 39.659639],
                content: `<p style="text-align:center;font-weight:bold;">唐山沐竺教育科技有限公司</p><p>唐山市路北区卫国北路金港怡园底商329-2</p>`,
                visible: true,
                events: {
                    close() {
                    // console.log('close infowindow1');
                    }
                }
                }
            ],
            slotWindow: {
                position: [118.164550, 39.659639]
            },
            currentWindow: {
                position: [0, 0],
                content: "",
                events: {},
                visible: false
            },
            amapManager,
            events: {
                init(o) {
                let marker = new AMap.Marker({
                    position: [118.164550, 39.659639]
                });

                marker.setMap(o);
                }
            },
            iconList:[
                {
                    id:1,
                    title:'唐山沐竺教育科技有限公司',
                    text:'地址：唐山市路北区卫国北路金港怡园底商329-2',
                    icon:'icondituzuobiao'
                },
                {
                    id:2,
                    title:'邮箱',
                    text:'924072404@qq.com',
                    icon:'iconxinfengxinjian'
                },
                {
                    id:3,
                    title:'联系方式',
                    text:'联系电话：400-676-0969',
                    icon:'icondianhuahuchu'
                }
            ],
            ruleForm: {
                name: '',
                tel: '',
                email: '',
                msg: ''
            },
            rules: {
                name: [
                    { required: true, message: '请输入您的姓名', trigger: 'blur' },
                ],
                tel: [
                    {validator: checkPhone, required: true, trigger: 'change' }
                ],
                email: [
                    { validator: checkEmail, required: true, trigger: 'change' }
                ],
                msg: [
                    { required: true, message: '请留言您想说的话', trigger: 'change' }
                ],
            },
            textRow:4,
            formSpan:20,
            imgUrl:'',
            winWidth:''
        }
    },
    components:{
        Banner
    },
    mounted(){
        this.currentWindow = this.windows[0];
        this.winWidth = document.body.offsetWidth || document.documentElement.offsetWidth;
        if (this.winWidth<768){
            this.textRow = 2;
            this.formSpan = 22;
        }
    },
    created(){
        this.getBanner();
    },
    methods:{
        ...mapMutations(['toggleToast','setTitle']),
        getBanner(){
            this.$http.get('/api/show/TopPic',{
                params:{
                    id:13
                }
            }).then(res=>{
                if (res.data.state === 200){
                    this.imgUrl = res.data.data.cat_pic;
                }else{
                    this.imgUrl = '';
                }
            })
        },
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$http.get('/api/show/addData',{
                        params:{
                            type:2,
                            tel:this.ruleForm.tel,
                            username:this.ruleForm.name,
                            email:this.ruleForm.email,
                            content:this.ruleForm.msg
                        }
                    }).then(res=>{
                        if(res.data.state === 200 ){
                            if (this.winWidth>=768){
                                this.$alert('提交成功','提示',{
                                    callback:()=>{
                                        this.$refs[formName].resetFields();
                                    }
                                });
                            }else{
                                this.toggleToast(true);
                                this.setTitle('提交成功');
                                setTimeout(()=>{
                                    this.toggleToast(false);
                                    this.$refs[formName].resetFields();
                                },2000)
                            }
                            
                        }else{
                            if(this.winWidth >=768){
                                this.$alert('提交失败,请稍后重试','提示');
                            }else{
                                this.toggleToast(true);
                                this.setTitle('提交失败,请稍后重试');
                                setTimeout(()=>{
                                    this.toggleToast(false);
                                },2000)
                            }
                        }
                    })
                } else {
                    return false;
                }
            });
        },
    }
}
</script>
