<template>
  <div class="banner-list">
    <swiper :options="swiperOption" ref="SwiperBanner" id="swiper_container">
      <swiper-slide v-for="item in list" :key="item.id">
          <img :src="`${imgPath}${item.content}`" alt />
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination" id='swiper-pagination'></div>
    </swiper>
  </div>
</template>
<script>
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";
import {IMAGE_PATH} from '../common/pathConfig.js'
export default {
  name: "SwiperBanner",
  props:{
    list:{
      type:Array,
      default(){
        return []
      }
    }
  },
  data() {
    return {
      swiperOption: {
        autoplay: true,
        loop: true,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        },
        observer: true, 
        observeParents: true
      },
      imgPath:IMAGE_PATH
    };
  },
  computed: {
    swiper() {
      return this.$refs.SwiperBanner.swiper;
    }
  },
  mounted(){
    var comtainer = document.getElementById('swiper-pagination');
      comtainer.onmouseenter = () =>{
        this.swiper.autoplay.stop();
      };
      comtainer.onmouseleave = ()=> {
        this.swiper.autoplay.start();
      }
  },
  components: {
    swiper,
    swiperSlide
  },
};
</script>

