            <li v-for="(item,index) in tabs" :key="index">
              <router-link :to="item.path" class="item-link">{{item.text}}</router-link>
            </li>
          </ul>
          <ul class="navbar-nav navbar-right">
            <div class="search">
            	<input class="input" type="text" placeholder="请输入关键字" v-model="searchText" @keyup.enter="search">
				<span class="iconfont iconfangdajingsousuo" @click="search"></span>
            </div>
			<div class="text-no" v-if="textShow">
				<p>暂无此搜索项</p>
