			<input class="input" type="text" placeholder="请输入关键字" v-model="searchText" @keyup.enter="search">
			<span class="iconfont iconfangdajingsousuo" @click="search"></span>
        </div> -->
      <div class="menu" @click.stop="showMenu">
        <span class="glyphicon glyphicon-menu-hamburger"></span>
      </div>
    </div>
    <div class="mobile-mask" v-if="tabShow" @click="tabShow=false">
      <ul class="mask-list">
