<script>
import {mutations} from "../store";
import {mapMutations} from 'vuex'
export default {
  data() {
    return {
		searchText:'',
      	tabs: [
			{
			id:1,
			text:'首页',
			path:'/home'
			},{
			id:2,
			text:'关于我们',
			path:'/about'
			},{
			id:3,
			text:'课程体系',
			path:'/course'
			},{
			id:4,
			text:'新闻动态',
			path:'/news'
			},{
			id:5,
			text:'招商加盟',
			path:'/join'
			},{
			id:6,
			text:'联系我们',
			path:'concat'
			}
      	],
		isShow: "/home",
		tabShow: false,
		tabId: 50,
		textShow:false
    };
  },
  watch: {
    tabShow (val, oldVal) {
        this.setTabShow(val);
    },
    $route: {
      handler: function(val, oldVal) {
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
        this.isShow = val.path;
        this.tabShow = false;
        this.tabId = "";
      },
      // 深度观察监听
      deep: true
    }
  },
  created() {
    this.isShow = this.$route.path;
  },
  mounted() {
    const w = document.body.offsetWidth || document.documentElement.offsetWidth;
    if (w <= 768) {
      document.onclick = () => {
        this.tabShow = false;
      };
	}
	$(document).scroll(function(){
		const sTop =  $(document).scrollTop() || $('body').scrollTop();
		if (sTop>20){
			$('.head').addClass('fixed');
		}else{
			$('.head').removeClass('fixed');
		}
	})
  },
  methods: {
	setTabShow: mutations.setTabShow,
	...mapMutations(['toggleShow']),
	search(){
		console.log(3);
		// 课 课程 机器人 编程 少儿编程 乐高课  乐高机器人  大颗粒 小颗粒 EV3 ------课程体系
		// 合作 加盟 开店 招商加盟 ------招商加盟
		// 报名 在线报名  ------在线报名弹窗
		// 公司  学校地址  ------联系我们
		// 机器人比赛   赛事    比赛   ------跳转到最新上传的国际性赛事

		const text = this.searchText;
		this.textShow = false;
		if(text === '课'||text==='课程'||text==='机器人'||text==='编程'||text==='少儿编程'||text==='乐高课'||text==='乐高机器人'||text==='大颗粒'||text==='小颗粒'||text==='EV3'){
			this.$router.push({path:'/course'})
		}else if(text==='合作'||text === '加盟'|| text==='开店'||text==='招商加盟'){
			this.$router.push({path:'/join'})
		}else if (text === '报名' || text === '在线报名'){
			this.toggleShow(true)
		}else if (text==='公司'||text==='学校地址'){
			this.$router.push({path:'/concat'})
		}else if(text==='机器人比赛'||text==='赛事'||text==='比赛'){
			const path = Math.floor(Math.random()*(5-1+1))+1;
			this.$router.push({path:'/match'+path})
		}else{
			this.textShow = true;
		}
	}, 
    goTop(path) {
      const curPath = this.$route.path;
      if (path == curPath) {
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
      }
    },
    switchMenu(id) {
      this.tabId = id;
    },
    showMenu() {
      this.tabShow = !this.tabShow;
    }
  }
};
</script>


