<template>
<div>
    <div class="list list1">
        <swiper :options="swiperOption" ref="mySwiper" >
            <swiper-slide v-for="item in list" :key="item.arc_id">
                <dl class="case">
                    <dt>
                        <img :src="imgPath+item.litpic" alt="">
                    </dt>
                    <dd>
                        <p>{{item.description}}</p>
                    </dd>
                </dl>
            </swiper-slide>
            <div class="swiper-button-prev" slot="button-prev"></div>
            <div class="swiper-button-next" slot="button-next"></div>
        </swiper>
    </div>
</div>
</template>
<script>
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";
import { IMAGE_PATH } from "../common/pathConfig.js";
export default {
    props: {
        list: {
            type:Array,
            default() {
                return []
            }
        }
    },
    data () {
        return {
            swiperOption: {
                autoplay: false,
                navigation: {
                    nextEl: '.swiper-button-next',
                    prevEl: '.swiper-button-prev'
                },
                slidesPerView: 3,
                slidesPerGroup: 3
            },
            imgPath: IMAGE_PATH,
            winWidth: ''
        };
    },
    computed: {
        swiper () {
            return this.$refs.mySwiper.swiper;
        }
    },
    components: {
        swiper,
        swiperSlide
    },
    mounted() {
        this.winWidth = document.body.offsetWidth || document.documentElement.offsetWidth;
        if( this.winWidth < 992 ){
            this.$set(this.swiperOption,'slidesPerView',1);
            this.$set(this.swiperOption,'slidesPerGroup',1);
            this.$refs.mySwiper.swiper.destroy()   //先销毁
            this.$refs.mySwiper.mountInstance()   //后在加载
        }
    }
};
</script>

