<template>
    <div>
        <Banner :imgUrl='imgUrl'/>
        <div class="course-icon">
            <div class="container">
                <div class="row">
                    <div class="col-xs-3 col-sm-3 col-md-3" v-for="item in iconList" :key="item.id">
                        <p class="icon" :class="'iconfont '+ item.icon"></p>
                        <p class="text">{{item.text}}</p>
                    </div>
                </div>
            </div>
        </div>
        <div class="course-system">
            <div class="title">课程体系</div>
            <div class="container">
                <div class="row">
                    <div class="col-xs-3 col-sm-3 col-md-3 left">
                        <div v-for="item in list" :key="item.id" class="item" :class="item.id==tabId?'active':''">
                            <p class="title" @click.prevent="Open(item)">
                                <span>{{item.name}}</span>
                            </p>
                            <ul v-if="item.id==tabId">
                                <li
                                v-for="sub in item.son"
                                :key="sub.id"
                                class="subitem"
                                :class="{active:sub.id === subId}"
                                @click="getDetail(sub.id)"
                                >{{sub.name}}</li>
                            </ul>
                        </div>
                    </div>
                    <div class="col-xs-9 col-sm-9 col-md-9 right">
                        <div class="box">
                            <div v-html="content"></div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="course-show" v-if="courseList.length">
             <div class="title">课程展示</div>
           <div class="container">
                <SwiperList :list='courseList'/>
           </div>
        </div>
    </div>
</template>
<script>
import Banner from '../../components/Banner';
import { IMAGE_PATH } from "../../common/pathConfig.js";
import SwiperList from '../../components/SwiperList.vue';
export default {
    data () {
        return {
            iconList:[
                {
                    id:1,
                    text:'教育课程',
                    icon:'iconkecheng'
                },{
                    id:2,
                    text:'生态设计',
                    icon:'iconshejishi'
                },{
                    id:3,
                    text:'少儿编程',
                    icon:'iconbiancheng'
                },{
                    id:4,
                    text:'平台服务',
                    icon:'iconfuwu'
                }
            ],
            list:[],
            imgUrl: '',
            tabId: 1,
            subId: 2,
            content: "",
            imgPath: IMAGE_PATH,
            imgUrl:'',
            courseList:[]
        }
    },
    components:{
        Banner,
        SwiperList
    },
    created(){
        this.getBanner();
        this.getData();
        this.getCourseShow();
    },
    methods: {
        Open(item) {
            if (this.tabId == item.id) {
                this.tabId = "";
            } else {
                this.tabId = item.id;
            }
        },
        //课程体系右侧详情
        getDetail(id){
            this.subId = id;
            this.$http.get('/api/show/index',{
                params:{
                    id:id
                }
            }).then(res=>{
                if (res.data.state === 200){
                    const str = res.data.data? res.data.data[0].content:'' ;
					let newStr = null;
                    const imgSrc = str.match(/[sS][rR][cC][\s]*=[\s']*(?<ref_value>.*?(\.gif|\.jpg|\.png))/gi);
                    let imgRes = [];
                    imgSrc && imgSrc.forEach(item => {
                        imgRes.push(item.substr(6));
                    });
                    imgRes.forEach(item => {
                        const regExp = new RegExp(`/${item}`, 'gi');
                        newStr = str.replace(regExp, `${this.imgPath}/${item}`);
                    });
                    this.content = newStr || str;
                }
            })
        },
         //课程体系 左侧导航
        getData() {
            this.$http.get('/api/show/course',{
                params:{
                    pid:17
                }
            }).then(res=>{
                if(res.data.state === 200){
                    this.list = res.data.data || [];
                    this.tabId = res.data.data[0].id;
                    this.subId = res.data.data[0].son[0].id;
                    this.getDetail(this.subId);
                }
            })
        },
        //banner
        getBanner(){
            this.$http.get('/api/show/TopPic',{
                params:{
                    id:10
                }
            }).then(res=>{
                if (res.data.state === 200){
                    this.imgUrl = res.data.data.cat_pic;
                }else{
                    this.imgUrl = '';
                }
            })
        },
        //课程展示
        getCourseShow(){
            this.$http.get('/api/show/index',{
                params:{
                    id:18
                }
            }).then(res=>{
                if (res.data.state === 200){
                    this.courseList = res.data.data || [];
                }
            })
        }
    }
}
</script>
