<script>
	import {mapMutations} from 'vuex'
	export default {
		data() {
			return {
				tabShow:true
			};
		},
		mounted() {
			const w = document.body.offsetWidth || document.documentElement.offsetWidth;
			if (w <= 1024) {
				this.tabShow = false;
			}
  		},
		methods: {
			goItem(path,id) {
				const curPath = this.$route.path;
				document.body.scrollTop = 0;
				document.documentElement.scrollTop = 0;
				if (path != curPath && id) {
					this.$router.push({
						path: path,
						query:{
							id:id||''
						}
					});
				}else if (path != curPath){
					this.$router.push({
						path: path
					});
				}
			},
			showInfo(){
				this.toggleShow(true);
			},
			...mapMutations(['toggleShow'])
		},
	};
</script>

