<script>
import SwiperBanner from '../../components/SwiperBanner.vue';
import CourseSwiper from '../../components/CourseSwiper.vue';
import { swiper, swiperSlide } from 'vue-awesome-swiper';
import {IMAGE_PATH} from '../../common/pathConfig.js';
import {mapState,mapMutations} from 'vuex';

export default {
    data () {
        return {
            swiperOption: {
                autoplay:true,
                navigation: {
                    nextEl: '.swiper-button-next',
                    prevEl: '.swiper-button-prev',
                },
                slidesPerView : 4,
                slidesPerGroup : 4,
            },
            phone:'',
            winWidth:'',
            type:4,
            bannerList:[],
            aboutUs:[],
            imgPath:IMAGE_PATH,
            courseLeft:[],
            courseRight:[],
            matchList:[],
            certificateRow:[],
            certificateColum:[],
            newsList:[]
        }
    },
    mounted(){
        this.winWidth = document.body.offsetWidth || document.documentElement.offsetWidth;
        if( this.winWidth < 992 ){
            this.$set(this.swiperOption,'slidesPerView',1);
            this.$set(this.swiperOption,'slidesPerGroup',1);
            this.$refs.MatchSwiper.swiper.destroy()   //先销毁
            this.$refs.MatchSwiper.mountInstance()   //后在加载
        }
    },
    components:{
        SwiperBanner,
        CourseSwiper,
        swiper,
        swiperSlide
    },
    computed: {
        swiper() {
            return this.$refs.MatchSwiper.swiper
        },
   
    },
    created(){
        this.getBanner();
        this.getAbout();
        this.getCourse();
        this.getMatch();
        this.getCertificate();
        this.getNews();
    },
    methods:{
        //领取课程
         ...mapMutations(['toggleToast','setTitle']),
        receiveCourse(){
           const reg = /^1[3|4|5|7|8][0-9]{9}$/;
           if (this.phone === '') {
               if (this.winWidth>=768){
                   this.$alert('请输入手机号','提示');
               }else{
                  this.type=1;
               }
                
                return;
            }
            if (!reg.test(this.phone)) {
                if (this.winWidth>=768){
                    this.$alert('手机号格式错误','提示');
                }else{
                  this.type=2;
                }
                return;
            }
            this.type=4;
            this.$http.get('/api/show/addData',{
                params:{
                    type:3,
                    tel:this.phone
                }
            }).then(res=>{
                if(res.data.state === 200 ){
                    if(this.winWidth >=768){
                        this.$alert('提交成功','提示',{
                            callback:()=>{
                                this.phone = '';
                            }
                        });
                    }else{
                        this.toggleToast(true);
                        this.setTitle('提交成功');
                        setTimeout(()=>{
                            this.toggleToast(false);
                            this.phone = '';
                        },2000)
                    }
                }else{
                    if(this.winWidth >=768){
                         this.$alert('提交失败,请稍后重试','提示');
                    }else{
                        this.toggleToast(true);
                        this.setTitle('提交失败,请稍后重试');
                        setTimeout(()=>{
                            this.toggleToast(false);
                        },2000)
                    }
                }
            })
        },
        //新闻详情
        goDetail(id){
            this.$router.push({path:'/news/newsDetail',query:{cid:11,arcId:id}})
        },
        //banner
        getBanner(){
            this.$http.get('/api/show/banner').then(res=>{
                if(res.data.state===200){
                    this.bannerList = res.data.data || [];
                }else{
                    this.bannerList = [];
                }
            })
        },
        //关于乐思
        getAbout(){
            this.$http.get('/api/show/index',{
                params:{
                    id:2
                }
            }).then(res=>{
                if(res.data.state === 200){
                    this.aboutUs = res.data.data || [];
                }
            })
        },
        //课程展示
        getCourse(){
            this.$http.get('/api/show/index',{
                params:{
                    id:4
                }
            }).then(res=>{
                if(res.data.state === 200){
                    this.courseLeft = res.data.data || [];
                }
            });
            this.$http.get('/api/show/index',{
                params:{
                    id:5
                }
            }).then(res=>{
                if(res.data.state === 200){
                    this.courseRight = res.data.data || [];
                }
            })

        },
        //大赛展示
        getMatch(){
            this.$http.get('/api/show/index',{
                params:{
                    id:6
                }
            }).then(res=>{
                if(res.data.state === 200){
                    this.matchList = res.data.data || [];
                }
            })
        },
        //证书展示
        getCertificate(){
            this.$http.get('/api/show/index',{
                params:{
                    id:27
                }
            }).then(res=>{
                if(res.data.state === 200){
                    this.certificateRow = res.data.data || [];
                }
            })
            this.$http.get('/api/show/index',{
                params:{
                    id:28
                }
            }).then(res=>{
                if(res.data.state === 200){
                    this.certificateColum = res.data.data || [];
                }
            })
        },
        getNews(){
            this.$http.get('/api/show/NewList',{
                params:{
                    id:11,
                    page:1
                }
            }).then(res=>{
                console.log(res);
                if(res.data.state === 200){
                    this.newsList = res.data.data || [];
                }
            })
        }

    }
     
}
</script>
