<template>
    <div class="join">
        <Banner :imgUrl='imgUrl'/>
        <Item :list="list"/>
        <div class="message">
            <div class="msg-box">
                <div class="form-box">
                    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                        <el-col :span="formSpan">
                            <el-form-item label="姓名：" prop="name">
                                <el-input v-model="ruleForm.name" placeholder="请输入您的姓名"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="formSpan">
                            <el-form-item label="电话：" prop="tel">
                                <el-input v-model="ruleForm.tel" placeholder="请输入您的电话"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="formSpan">
                            <el-form-item label="微信：" prop="wx">
                                <el-input v-model="ruleForm.wx" placeholder="请输入您的微信"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="formSpan">
                            <el-form-item label="留言：" prop="msg">
                                <el-input type="textarea" :rows="textRow"  v-model="ruleForm.msg"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="formSpan">
                            <el-form-item>
                                <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
                            </el-form-item>
                        </el-col>
                    </el-form>
                </div>
            </div>
        </div>
        <div class="national-center">
            <div class="title">全国中心</div>
            <div class="container">
                <dl class="item" v-for="item in centerList" :key="item.arc_id">
                    <dt>
                        <img :src="imgPath+item.litpic" alt="">
                    </dt>
                    <dd>
                        <p>{{item.title}}</p>
                    </dd>
                </dl>
            </div>
        </div>
    </div>
</template>
<script>
import Banner from '../../components/Banner.vue';
import Item from '../../components/Item.vue';
import {IMAGE_PATH}  from '../../common/pathConfig.js';
import {mapState,mapMutations} from 'vuex';

export default {
    data () {
        var checkPhone = (rule, value, callback) => {
            if (!value) {
                return callback(new Error('手机号不能为空'));
            } else {
                const reg = /^1[3|4|5|7|8][0-9]\d{8}$/
                if (reg.test(value)) {
                    callback();
                } else {
                    return callback(new Error('请输入正确的手机号格式'));
                }
            }
        };
        return {
            list:[],
            ruleForm: {
                name: '',
                tel: '',
                wx: '',
                msg: ''
            },
            rules: {
                name: [
                    { required: true, message: '请输入您的姓名', trigger: 'blur' },
                ],
                tel: [
                    {validator: checkPhone, required: true, trigger: 'change' }
                ],
                wx: [
                    { required: true, message: '请输入您的微信', trigger: 'change' }
                ],
                msg: [
                    { required: true, message: '请留言您想说的话', trigger: 'change' }
                ],
            },
            textRow:4,
            formSpan:20,
            imgUrl:'',
            imgPath:IMAGE_PATH,
            centerList:[],
            winWidth:''
        }
    },
    components:{
        Banner,
        Item
    },
    created(){
        this.getBanner();
        this.getData();
        this.getCenter();
    },
    mounted(){
        this.winWidth = document.body.offsetWidth || document.documentElement.offsetWidth;
        if (this.winWidth<768){
            this.textRow = 2;
            this.formSpan = 24;
        }
    },
    methods:{
        ...mapMutations(['toggleToast','setTitle']),
        getBanner(){
            this.$http.get('/api/show/TopPic',{
                params:{
                    id:12
                }
            }).then(res=>{
                if (res.data.state === 200){
                    this.imgUrl = res.data.data.cat_pic;
                }else{
                    this.imgUrl = '';
                }
            })
        },
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$http.get('/api/show/addData',{
                        params:{
                            type:0,
                            tel:this.ruleForm.tel,
                            username:this.ruleForm.name,
                            weixin:this.ruleForm.wx,
                            content:this.ruleForm.msg
                        }
                    }).then(res=>{
                        if(res.data.state === 200 ){
                            if (this.winWidth>=768){
                                this.$alert('提交成功','提示',{
                                    callback:()=>{
                                        this.$refs[formName].resetFields();
                                    }
                                });
                            }else{
                                this.toggleToast(true);
                                this.setTitle('提交成功');
                                setTimeout(()=>{
                                    this.toggleToast(false);
                                    this.$refs[formName].resetFields();
                                },2000)
                            }
                            
                        }else{
                            if(this.winWidth >=768){
                                this.$alert('提交失败,请稍后重试','提示');
                            }else{
                                this.toggleToast(true);
                                this.setTitle('提交失败,请稍后重试');
                                setTimeout(()=>{
                                    this.toggleToast(false);
                                },2000)
                            }
                        }
                    })
                } else {
                    return false;
                }
            });
        },
        getData(){
            this.$http.get('/api/show/index',{
                params:{
                    id:29
                }
            }).then(res=>{
                if (res.data.state === 200){
                    this.list = res.data.data;
                }
            })
        },
        getCenter(){
            this.$http.get('/api/show/index',{
                params:{
                    id:19
                }
            }).then(res=>{
                if (res.data.state === 200){
                    this.centerList = res.data.data || [];
                }
            })
        }
    }
}
</script>
