<template>
    <div>
        <Banner :imgUrl='imgUrl'/>
        <div class="body">
            <img :src="imgPath+imgTop" alt="">
        </div>
        <div class="list">
           <div class="container">
                <dl v-for="item in topList" :key="item.id">
                    <dt>
                        <img :src="imgPath+item.litpic" alt="">
                    </dt>
                    <dd>{{item.title}}</dd>
                </dl>
                
           </div>
        </div>
        <div class="body">
            <img :src="imgPath+imgTwo" alt="">
        </div>
        <div class="course-item" v-if="courseList.length>0">
            <div class="title" >课程特色</div>
            <div class="container">
                <div class="row" v-for="index in Math.ceil(courseList.length/3)" :key="index">
                    <div class="col-xs-12 col-sm-4 col-md-4" v-for="(item,key) in courseList.slice((index-1)*3,index*3)" :key="item.id">
                        <dl>
                            <dt>{{(key+1) + (index - 1) * 3}}</dt>
                            <dd><p>{{item.title}}</p></dd>
                        </dl>
                    </div>
                </div>
            </div>
        </div>
        <div class="feeb-back" v-if="msgList.length>0">
            <div class="title" :class="age==3?'age3':age==6?'age6':age==9?'age9':''">听他们这样说</div>
            <div class="container">
                <div class="row" v-for="index in Math.ceil(msgList.length/4)" :key="index">
                    <div class="col-xs-12 col-sm-6 col-md-3" v-for="item in msgList.slice((index-1)*4,index*4)" :key="item.id">
                        <dl>
                            <dt>
                                <img :src="imgPath+item.litpic" alt="">
                            </dt>
                            <dd :class="age==3?'age3-bg':age==6?'age6-bg':age==9?'age9-bg':''">
                                <div v-html="item.content"></div>
                            </dd>
                        </dl>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>
<script>
import Banner from '../../components/Banner';
import {IMAGE_PATH} from '../../common/pathConfig.js';
export default {
    data () {
        return {
            age:3,
            imgPath:IMAGE_PATH,
            imgUrl:'',
            imgTop:'',
            topList:[],
            imgTwo:'',
            courseList:[],
            msgList:[],
            id:''

        }
    },
    components:{
        Banner
    },
    mounted(){
        const id = this.$route.query.id;
        this.getBanner(id);
        this.getData(39,40,41,42);
    },
    methods:{
        getData(id1,id2,id3,id4){
            this.getTop(id1);
            this.getTwo(id2);
            this.getCourse(id3);
            this.getMsg(id4);
        },
        getBanner(id){
            this.$http.get('/api/show/TopPic',{
                params:{
                    id:id
                }
            }).then(res=>{
                if (res.data.state === 200){
                    this.imgUrl = res.data.data.cat_pic;
                }else{
                    this.imgUrl = '';
                }
            })
        },
        getTop(id){
            this.$http.get('/api/show/TopPic',{
                params:{
                    id:id
                }
            }).then(res=>{
                if (res.data.state === 200){
                    this.imgTop = res.data.data.cat_pic;
                }else{
                    this.imgTop= '';
                }
            })
            this.$http.get('/api/show/index',{
                params:{
                    id:id
                }
            }).then(res=>{
                if (res.data.state === 200){
                    this.topList = res.data.data;
                }else{
                     this.topList = []
                }
            })
        },
        getTwo(id){
            this.$http.get('/api/show/TopPic',{
                params:{
                    id:id
                }
            }).then(res=>{
                if (res.data.state === 200){
                    this.imgTwo = res.data.data.cat_pic;
                }else{
                    this.imgTwo = '';
                }
            })
        },
        getCourse(id){
            this.$http.get('/api/show/index',{
                params:{
                    id:id
                }
            }).then(res=>{
                if (res.data.state === 200){
                    this.courseList = res.data.data;
                }else{
                     this.courseList = []
                }
            })
        },
        getMsg(id){
            this.$http.get('/api/show/index',{
                params:{
                    id:id
                }
            }).then(res=>{
                if (res.data.state === 200){
                    this.msgList = res.data.data;
                }else{
                     this.msgList = []
                }
            })
        }
    }
}
</script>

