<template>
    <div>
        <Banner :imgUrl='imgUrl'/>
        <div class="news">
            <div class="container">
                <div class="title">新闻动态</div>
                <div class="row" v-for="item in list" :key="item.arc_id">
                    <div class="item">
                        <dl>
                            <dt>
                                <img :src="imgPath+item.litpic" alt="">
                            </dt>
                            <dd>
                                <p class="title">{{item.title}}</p>
                                <p class="time">发布时间：{{item.publish_time.slice(0,10)}}</p>
                                <p class="con">
                                    {{item.description}}
                                </p>
                                <p class="more" @click="goDetail(item.cid,item.arc_id)">了解更多</p>
                            </dd>
                        </dl>
                    </div>
                </div>
            </div>
            <Page :total='total' @handleCurrentChangeSub='pageChange'/>
        </div>
    </div>
</template>
<script>
import Banner from '../../components/Banner'
import Page from '@/components/Page.vue';
import {IMAGE_PATH} from '../../common/pathConfig.js'
export default {
    data () {
        return {
            total:20,
            imgUrl:'',
            list:[],
            imgPath:IMAGE_PATH
        }
    },
    components:{
        Banner,
        Page
    },
    created(){
        this.getBanner();
        this.getList(1);
    },
    methods:{
        goDetail(cid,id){
            this.$router.push({path:'/news/newsDetail',query:{cid:cid,arcId:id}})
        },
        pageChange(page){
            this.getList(page);
        },
        getBanner(){
            this.$http.get('/api/show/TopPic',{
                params:{
                    id:11
                }
            }).then(res=>{
                if (res.data.state === 200){
                    this.imgUrl = res.data.data.cat_pic;
                }else{
                    this.imgUrl = '';
                }
            })
        },
        getList(page){
            this.$http.get('/api/show/NewList',{
                params:{
                    id:11,
                    page:page
                }
            }).then(res=>{
                if (res.data.state === 200){
                    this.list = res.data.data;
                    this.total = Number(res.data.total);
                }
            })
        }
    }
}
</script>
